import { Component } from '@angular/core';
import { IonicPage, LoadingController, NavController, NavParams } from 'ionic-angular';

import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { ICompany, ICompanyEdition } from '../../interfaces';
import { CompanyService } from '../../services';

import { first } from 'rxjs/operators';

@IonicPage({
  segment: 'company/:companyKey/edit'
})
@Component({
  selector: 'page-company-edit',
  templateUrl: 'company-edit.html',
})
export class CompanyEditPage {
  form: FormGroup;

  showForm: boolean;

  companyKey: string;
  company:ICompany;

  loading: any;

  constructor(
    private fb: FormBuilder,
    private companyService: CompanyService,
    public loadingCtrl: LoadingController,
    public navCtrl: NavController,
    public navParams: NavParams
  ) {
    this.companyKey = this.navParams.get('companyKey');
  }

  ionViewDidLoad() {
    this.getCompany();
  }

  private getCompany(){
    this.companyService
      .getOne(this.companyKey)
      .pipe(first())
      .subscribe((company:ICompany) => {
        this.company = company;

        this.createForm();

        this.showForm = true;
      }, error => {
        console.error(error);
      });
  }


  private createForm() {
    const {
      name,
      category,
      description,
      address,
      officeHours,
      paymentMethod
    } = this.company;

    this.form = this.fb.group({
      name: [name, [
        Validators.required
      ]],
      category: [category, [
        Validators.required
      ]],
      description:[description],
      address:[address, [
        Validators.required
      ]],
      officeHours:[officeHours, [
        Validators.required
      ]],
      paymentMethod:[paymentMethod, [
        Validators.required
      ]]
    });
  }

  submitForm(){
    if(this.form.invalid){ return; }

    this.loading = this.loadingCtrl.create({
      content: 'Aguarde...',
      dismissOnPageChange: true
    });

    this.loading.present();

    const {
      name,
      category,
      description,
      address,
      officeHours,
      paymentMethod
    } = this.form.value;

    const editCompany:ICompanyEdition = {
      name,
      category,
      description,
      address,
      officeHours,
      paymentMethod
    };

    this.companyService
      .updateOne(this.company.key, editCompany)
      .then(() => {
        this.loading.dismiss();
        this.navCtrl.getPrevious().data.companyKey = this.company.key;
        this.navCtrl.pop();
      }, error => {
        console.error(error);
        this.loading.dismiss();
      });
  }
}



